/* ================================== */
/*          GENERAL SETTINGS          */
/* ================================== */
html {
  box-sizing: border-box;
  font-size: 16px; }

*,
*::after,
*::before {
  box-sizing: inherit; }

body {
  color: #8096b7;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  background-color: #232e40;
  background-image: linear-gradient(135deg, #232e40, #191f28, #232e40, #191f28); }

ul {
  list-style: none; }

ul li {
  display: inline-block; }

ol li {
  display: block;
  list-style: none; }

/* ------------- LAYOUT ------------- */
/* ================================== */
/*             MAIN HEADER            */
/* ================================== */
.header {
  grid-area: header;
  display: flex;
  justify-content: space-between;
  padding-top: 20px;
  padding-right: 20px;
  padding-bottom: 20px;
  padding-left: 20px;
  box-shadow: 0 2px 25px rgba(0, 0, 0, 0.3);
  font-weight: 600;
  color: #ffffff;
  background-color: #a90b56; }
  .header__logo {
    align-self: center;
    font-size: 36px; }
  .header__heading {
    align-self: center;
    font-size: 36px;
    text-transform: uppercase; }
  .header__nav {
    align-self: center; }
  .header__list {
    font-size: 15px;
    font-weight: 400; }
  .header__item {
    height: 30px;
    margin-top: 0;
    margin-right: 3px;
    margin-bottom: 0;
    margin-left: 3px;
    border-radius: 50px;
    line-height: 30px;
    white-space: nowrap;
    color: #a90b56;
    background-color: #ffffff;
    cursor: pointer; }
  .header__nav-open-icon {
    position: fixed;
    top: 10px;
    right: 20px;
    z-index: 100;
    width: 60px;
    height: 60px;
    color: #ffffff;
    background-color: #a90b56;
    cursor: pointer; }
  .header__nav-open-icon .material-icons--big {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 40px; }

/* ================================== */
/*             NAVIGATION             */
/* ================================== */
.nav {
  position: fixed;
  z-index: 1;
  width: 100%;
  min-height: 100vh;
  background-color: rgba(169, 11, 85, 0.85); }
  .nav__list {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: nowrap;
    width: 100%;
    min-height: 100vh; }
  .nav__item {
    display: block;
    box-sizing: border-box;
    width: 50%;
    padding-top: 16px;
    padding-right: 16px;
    padding-bottom: 16px;
    padding-left: 16px;
    font-size: 60px;
    font-weight: 100;
    text-align: center; }
  .nav__link::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 0;
    transform: translate(-50%, -50%);
    background-color: #ffffff; }
  .nav__link:hover::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 1px;
    transform: translate(-50%, -50%);
    transition-property: width;
    transition-duration: 0.3s;
    transition-delay: ease-in-out;
    background-color: #ffffff; }
  .nav__link {
    position: relative;
    display: inline-block;
    width: 100%;
    height: 100%;
    color: #ffffff;
    text-decoration: none; }

.main {
  grid-area: content; }

/* ================================== */
/*               FOOTER               */
/* ================================== */
.footer {
  grid-area: footer;
  padding-top: 190px;
  padding-bottom: 5px;
  text-align: center;
  font-size: 12px;
  color: #dbe7f8;
  background-image: url("../assets/img/footer.svg");
  background-size: cover;
  background-position: center center; }
  .footer__link:link, .footer__link:visited, .footer__link:active, .footer__link:focus {
    color: currentColor;
    text-decoration: none; }
  .footer__link:hover {
    text-decoration: underline; }

/* ------------- PAGES -------------- */
/* ================================== */
/*            SETTINGS PAGE           */
/* ================================== */
/* ------------- SIDEBAR ------------- */
.author {
  grid-area: sidebar;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  margin-top: 100px; }
  .author__picture-box {
    padding-top: 10px;
    padding-right: 20px;
    padding-bottom: 10px;
    padding-left: 20px; }
  .author__picture {
    width: 100%;
    height: auto;
    border: solid 5px #232e40;
    margin-top: -25px;
    box-shadow: 0 2px 25px rgba(0, 0, 0, 0.3); }
  .author__header {
    margin-top: -45px;
    padding-left: 20px;
    font-size: 30px;
    font-weight: 100;
    color: #dbe7f8; }
  .author__header-intro {
    margin-bottom: 10px;
    padding-left: 20px;
    font-size: 30px;
    font-weight: 100; }
  .author__description {
    padding-left: 20px;
    font-size: 14px;
    font-weight: 300; }

/* ------------- CONTENT ------------- */
.diary-day {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 100px;
  margin-right: 20px;
  box-shadow: 0 2px 25px rgba(0, 0, 0, 0.3);
  background-color: #232e40; }
  .diary-day__header {
    width: 100%;
    margin-top: -45px;
    font-size: 30px;
    color: #dbe7f8; }

.diary-tasks {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  position: relative;
  width: 50%;
  padding-top: 0;
  padding-right: 20px;
  padding-bottom: 20px;
  padding-left: 20px; }
  .diary-tasks__more {
    width: 10%;
    cursor: pointer; }
  .diary-tasks__more .material-icons {
    float: right;
    margin-right: -10px;
    font-size: 30px;
    color: #a90b56; }
  .diary-tasks__header {
    width: 90%;
    font-size: 30px;
    font-weight: 100;
    margin: 0; }
  .diary-tasks__footer {
    font-size: 12px; }
  .diary-tasks__picture-box {
    width: 100%;
    height: 100px;
    margin-top: 10px;
    margin-right: 0;
    margin-bottom: 10px;
    margin-left: 0;
    overflow: hidden;
    background-color: #ffffff; }
  .diary-tasks__picture-box--picture-one {
    background-image: linear-gradient(135deg, rgba(203, 96, 179, 0.2), rgba(173, 18, 131, 0.47), rgba(143, 39, 99, 0.6)), url("../assets/img/programowanie.jpg");
    background-size: cover;
    background-position: center center;
    background-repeat: repeat-y; }
  .diary-tasks__picture-box--picture-two {
    background-image: linear-gradient(135deg, rgba(203, 96, 179, 0.2), rgba(173, 18, 131, 0.47), rgba(143, 39, 99, 0.6)), url("../assets/img/nauka.jpg");
    background-size: cover;
    background-position: center center;
    background-repeat: repeat-y; }
  .diary-tasks__picture-box--picture-three {
    background-image: linear-gradient(135deg, rgba(203, 96, 179, 0.2), rgba(173, 18, 131, 0.47), rgba(143, 39, 99, 0.6)), url("../assets/img/wedkarstwo.jpg");
    background-size: cover;
    background-position: center center;
    background-repeat: repeat-y; }
  .diary-tasks__content {
    min-height: 100px;
    line-height: 18px;
    font-size: 14px;
    font-weight: 300; }
  .diary-tasks__content .material-icons {
    font-size: 14px; }
  .diary-tasks__hr {
    width: 100%;
    height: 1px;
    margin-top: 10px;
    margin-right: 0;
    margin-bottom: 10px;
    margin-left: 0;
    border-bottom: solid 1px #4f5e74; }
  .diary-tasks__tags {
    width: 100%;
    margin-top: 30px;
    margin-right: 0;
    margin-bottom: 0;
    margin-left: 0; }

/* ================================== */
/*         MAIN GRID SETTINGS         */
/* ================================== */
/* ----------- DESKTOP LARGE---------- */
@media screen and (min-width: 1281px) {
  .wrapper {
    display: grid;
    grid-gap: 15px;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: [start header] 80px [sidebar] auto [content] auto [footer end];
    grid-template-areas: "header header header header" "sidebar content content content" "footer footer footer footer"; } }

/* ------------- DESKTOP ------------- */
@media screen and (max-width: 1280px) {
  .wrapper {
    display: grid;
    grid-gap: 15px;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: [start header] 80px [sidebar] auto [content] auto [footer end];
    grid-template-areas: "header header header header" "sidebar sidebar sidebar sidebar" "content content content content" "footer footer footer footer"; }
  .author {
    flex-flow: row nowrap; }
    .author__header-intro {
      margin-top: 0; }
    .author__profile {
      width: 50%; }
    .author__box-description {
      width: 50%; }
  .diary-day {
    margin-left: 20px; }
  .diary-tasks__header {
    font-size: 24px; } }

/* ------------- TABLET ------------- */
@media screen and (max-width: 1024px) {
  .wrapper {
    display: grid;
    grid-gap: 15px;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: [start header] 80px [sidebar] auto [content] auto [footer end];
    grid-template-areas: "header header header header" "sidebar sidebar sidebar sidebar" "content content content content" "footer footer footer footer"; }
  .nav__item {
    font-size: 55px; }
  .header__logo {
    font-size: 28px; }
  .header__heading {
    font-size: 28px; }
  .author {
    flex-flow: row wrap; }
  .author__profile {
    width: 50%; }
  .author__box-description {
    width: 50%; }
  .diary-tasks {
    width: 100%; }
  .diary-day {
    margin-left: 20px; }
  .diary-tasks__header {
    font-size: 36px; }
  .diary-tasks__picture-box {
    height: 200px; } }

/* ------------- PHONE -------------- */
@media screen and (max-width: 640px) {
  .wrapper {
    display: grid;
    grid-gap: 15px;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: [start header] 80px [sidebar] auto [content] auto [footer end];
    grid-template-areas: "header header header header" "sidebar sidebar sidebar sidebar" "content content content content" "footer footer footer footer"; }
  .nav__item {
    font-size: 45px; }
  .header__logo {
    font-size: 24px; }
  .header__heading {
    display: none; }
  .author {
    flex-flow: row wrap; }
  .author__profile {
    width: 100%; }
  .author__box-description {
    width: 100%; }
  .diary-tasks {
    width: 100%; }
  .diary-tasks__header {
    font-size: 28px; }
  .diary-day {
    margin-left: 20px; } }

/* ------------ COMPONENTS ----------- */
